
.main {
  width: $layout-size;
  margin: 0 auto;

  &__list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 50px;
  }

  &__item {
    margin-right: 48px;
    margin-bottom: 46px;

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-last-child(-n+4) {
      margin-bottom: 50px;
    }
  }
}
